import React from "react";
import '../css/index.css'
import '../js/index.js'
import '../css/container2.css'
import listimg1 from '../img/list1.png'
import listimg2 from '../img/list2.png'
import listimg3 from '../img/list3.png'

function Targeting() {
    return (
        <div className="container_2">
            <div className="main">
                <div className="top_content">
                    <div className="text_box">
                        <h1><span>"</span>정교<span>"</span>한 타겟팅은</h1>
                        <h1><span >풍부한 데이터</span>로부터</h1>
                        <p >국내시장 전역을 커버할 수 있는 풍부한 데이터를 활용하여</p>
                        <p>정교한 오디언스 타겟팅으로 광고 효과를 상승시킬 수 있습니다</p>
                        <h2>
                            <a href="#">크로스타겟의 DMP 더 보기</a>
                            <p className="bg"></p>
                        </h2>
                    </div>
                </div>
                <div className="bottom_content">
                    <div className="list">
                        <div className="boxs">
                            <div className="bot_textbox">
                                <h1>약 4,000만 오디언스</h1>
                                <h1>온 , 오프라인 데이터 활용</h1>
                                <p >빅데이터를 활용하여 핵심 타겟과</p>
                                <p>가장 유사한 사용자들을 찾아</p>
                                <p>맞춤형 오디언스 세그먼트를 생성합니다</p>
                            </div>
                            <div className="bot_imgbox">
                                <img  src={listimg1}></img>
                            </div>
                        </div>
                        <div className="boxs">
                            <div className="bot_textbox">
                                <h1>캠페인에 적합한 타겟을</h1>
                                <h1>정교하게 추출</h1>
                                <p >다른 성격의 다양한 오디언스를</p>
                                <p>필요에 따라 조합하여 캠페인의 목적에</p>
                                <p>적합한 타겟 추출이 가능합니다</p>
                            </div>
                            <div className="bot_imgbox">
                                <h2>유사 타겟 확장</h2>
                                <img src={listimg2}></img>
                            </div>
                        </div>
                        <div className="boxs">
                            <div className="bot_textbox">
                                <h1>광고 성과 향상 및</h1>
                                <h1>인사이트 분석</h1>
                                <p >타겟팅을 통한 성과 향상은 물론</p>
                                <p>정교한 1:1 분석을 통한</p>
                                <p>인사이트 리포트를 제공합니다</p>
                            </div>
                            <div className="bot_imgbox">
                                <h2>인사이트 리포트</h2>
                                <ul className="bot_imgbox_list">
                                    <li>Click</li>
                                    <li>CTR</li>
                                    <li>CPM</li>
                                    <li>CPC</li>
                                    <li>Impression</li>
                                </ul>
                                <img src={listimg3}></img>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Targeting;