import React from "react";
import '../css/index.css'
import '../js/index.js'
import clay_ctv from '../img/clay_ctv.png'
import clay_self from '../img/clay-self.png'
import header_bg from '../img/header.png'
import clay_phone from '../img/clay-phone.png'
import clay_iptv from '../img/clay-iptv.png'
import link from '../img/linkimg.png'

function Main() {
    return (
        <div className="container_1">
            <div className="section1">
                <div className="leftsec">
                    <h1>채널의 경계가 없는</h1>
                    <h1>데이터 마케팅 자동화를</h1>
                    <h1>제공합니다</h1>
                    <h4 >국경, 크리에이티브, 디바이스 제약을 뛰어넘는</h4>
                    <h4>광고 자동화를 통해 전세계 어디에나</h4>
                    <h4>광고주가 원하는 고객에게 메세지를 전달할 수 있습니다</h4>
                    <p>
                        <a href="#">문의하기</a>
                    </p>
                </div>
                <div className="rigthsec">
                    <div className="list">
                        <div className="box">
                            <img src={header_bg}></img>
                            <div className="center_img">
                                <img src={clay_ctv}></img>
                            </div>
                            <p>CTV</p>
                        </div>
                        <div className="box">
                            <img src={header_bg}></img>
                            <div className="center_img">
                                <img  src={clay_self}></img>
                            </div>
                            <p>Moblie</p>
                        </div>
                        <div className="box">
                            <img src={header_bg}></img>
                            <div className="center_img">
                                <img src={clay_phone}></img>
                            </div>
                            <p>Self-Serve 매체</p>
                        </div>
                        <div className="box">
                            <img src={header_bg}></img>
                            <div className="center_img">
                                <img src={clay_iptv}></img>
                            </div>
                            <p>IPTV</p>
                        </div>
                        <div className="center_box">
                            <img src={link}></img>
                        </div>
                    </div>
                </div>
                <div className="bg_animation">
                    <div className="animation_1"></div>
                    <div className="animation_2"></div>
                    <div className="animation_3"></div>
                    <div className="animation_4"></div>
                </div>
            </div>
        </div>
    )
}

export default Main;